	module.exports = {"data":{"markdownRemark":{"html":"<p>正式(?)にサークル化し、サイトも完全リニューアル。頻繁にサイト消滅していたが、人や人以外はやってくるのだろうか。 今までdanmaq始動は 2004/12/30 と思っていたが、過去ログが見つかり、どうやら 2004/11/28 からっぽい。(いずれにしても本始動は 2005/1/11 から)</p>\n<h2>リニューアルその後</h2>\n<p>2008年4月に現在のデザインとかなり近いものにリニューアル。 次のリニューアルは2011年6月。今回デザインは殆ど変っていませんが、中身が従来まで完全に手入力ブログだったものを自作ブログシステム<a href=\"http://nue.sourceforge.jp/\">Network Utterance Environment</a>で置き換えました。 <img src=\"https://danmaq.com/wp-content/uploads/2012/11/logo2012.png\"> さらに2012年12月にWordpressへ移行、白基調のページにして、ロゴも現在のものへと変更。マイグレーションが1割程度しかできていないにもかかわらず2015年にも大幅リニューアルで今に至る。はてさて……。</p>","frontmatter":{"cover":null,"date":"2004-11-28T00:00:00+09:00","strDate":"2004/11/27","title":"danmaq"}}},"pathContext":{"path":"/ja/blog/2004/11/28-danmaq-2/","langKey":"ja"}}

